import common from '@/utils/common.js';
import Bmob from "hydrogen-js-sdk";
import producers from '@/utils/producers.js';
import loading from "@/components/Loading/index.vue"

let that;
let producerId;
export default {
	components: {
		loading
	},
	data() {
		return {
			start_date: common.getDay(0, true,true),
			end_date: common.getDay(1, true),
			start_date_desc: '',
			end_date_desc: '',
			inMoney: 0,
			outMoney: 0,

			debt_list: [],
			name: '',
			loading: true,
		}
	},
	methods: {
		bindDate_startChange(e) {
			console.log(e)
			that.start_date = e.detail.value + ' 00:00:00';
			that.getList()
		},

		bindDate_endChange(e) {
			console.log(e)
			that.end_date = e.detail.value + ' 23:59:59';
			that.getList()
		},

		getList() {
			const query = Bmob.Query("order_opreations");
			query.equalTo("producer", "==", producerId);
			query.equalTo("type","==", 1);
			query.equalTo("extra_type","==", 5);
			query.equalTo("account","!=", null);
			query.include("opreater", "account");
			query.equalTo("createdAt", ">=", that.start_date);
			query.equalTo("createdAt", "<=", that.end_date);
			query.order("-createdAt");
			query.find().then(res => {
				that.loading = false
				that.debt_list = res

				for (let item of res) {
					if (item.type == -1) {
						that.inMoney += item.real_money
					} else if (item.type == 1) {
						that.outMoney += item.real_money
					}
				}
			});
		}
	},
	onLoad(options) {
		//console.log(options)
		that = this;
		that.name = options.name
		that.start_date_desc = that.start_date.split(" ")[0];
		that.end_date_desc = that.end_date.split(" ")[0];
		producerId = options.id;
		
		that.getList()
	},
}
